$main-grey: #dbe7ea;
$main-grey-alt: #bad0d7;
$darkgrey: #688a93;
$main-bggrey: #ebf2f3;

$blackbg: #243235;
$blackbg-lt: #2b3b3f;

$grey-txt: #A4A4A4;
$lightgrey-text: #c5c5c5;
$white-transparent: rgba(255, 255, 255, 0.95);
$grey-transparent: rgba(91, 144, 159, 0.20);
// 5B909F 20%
$title: #E7504F;
$topmenuheight: 50px;
$lettercube: 78px;

// Fonts
@import "./fontstyles";

// Common
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'PT-Sans-Expert', sans-serif;
    // font-size: 18px;
    // line-height: 27px;
}

a,
.aspan {
    text-decoration: none;
    color: $darkgrey;
    cursor: pointer;

    &:hover {
        color: black;
    }
}

body {
    // font-family: 'PT Sans', sans-serif;
    font-family: 'PT-Sans-Expert', sans-serif;
    // background-color: $blackbg;
}

button {
    font-family: 'PT-Sans-Expert', sans-serif;
}

li {
    list-style-type: none;
    padding-left: 20px;
}

.ptexpert {
    font-family: 'PT-Sans-Expert', sans-serif;
}

.fullwidth {
    width: 100%;
}

.redtext {
    color: $title;
}

.flexnowrap {
    display: flex;
    flex-wrap: nowrap;
}

.hidden {
    display: none;
    // position: absolute;
    // left: -9999px;
    // width: 1px;
    // margin: -1px;
}

.invisible {
    position: absolute;
    left: -9999px;
    width: 1px;
    height: 1px;
    margin: -1px;
}


// Main Containers
@import "./main_containers";


.flexcontainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1px;
}

.innercontainer {
    display: flex;
    width: 100%;
}


// Definitions

.defletterwrap {
    padding: 25px 20px 10px 0;
}

.defcontainer {
    display: flex;
    width: 100%;
}


// Top menu and footer

.top_menu {
    display: flex;
    gap: 2vw;
}

.top_name {
    display: flex;
    font-size: 21px;
    color: white;
    gap: 10px;
}

.top_name_mobmenu {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 21px;
    color: white;
    padding-right: 10px;
    gap: 20px;
}

.langgroupselect {
    align-self: center;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    width: fit-content;
    color: white;
    background: transparent;
    text-decoration: underline solid $darkgrey;
    text-transform: lowercase;
    -webkit-text-decoration: underline solid $darkgrey;
    border: none;
    font-size: 21px;
    // background-image: url("data:image/svg+xml;utf8,<svg fill='rgb(104,138,147)' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7.41 8.59 12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z'></path></svg>");
    // background-repeat: no-repeat;
    // background-position-x: 110%;
    // background-position-y: 60%;
    cursor: pointer;

    :active,
    :focus {
        background: transparent;
        border: none;
        outline: none;
        -webkit-box-shadow: none;
        box-shadow: none;
    }
}

.mobcsswrap {
    display: none;
}

.mobilemenuwrapwhite {
    padding: 0 20px;
    background-color: white;
}

.mobmenutitle {
    color: $darkgrey;
    padding: 0;
    font-size: 18px;
}

.mobmenubtn {
    border: none;
    background: transparent;
    color: $darkgrey;
    font-size: 18px;
    padding: 7px 0;
    cursor: pointer;
}

.mobmenubtnactive {
    color: white;
}

.burger_topmenu {
    display: none;
    cursor: pointer;
}

.top_menu a {
    text-decoration: none;
    font-size: 16px;
    line-height: 24px;
}

.mainmenubtn {
    border: none;
    background: transparent;
    color: $darkgrey;
    font-size: 18px;
    line-height: 27px;
    cursor: pointer;
}

.mainmenubtnactive {
    color: white;
}

.mainmenuruen {
    font-size: 12px;
    text-transform: uppercase;
}

.footer {
    padding: 0 20px;
    width: 100%;
    background-color: #243235;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    height: $topmenuheight;
    font-size: 12px;
    color: $darkgrey;
}




// Menu

.langmenufake {
    // width: calc(20% + 20px);
    width: 15vw;
    // min-width: 220px;
    padding: 30px 20px;
    text-align: left;
}

.langlistbutton {
    width: fit-content;
    margin: 4px 8px 4px 0;
    text-decoration: none;
    white-space: nowrap;
    padding: 5px 7px;
    font-size: 14px;
    text-align: left;
    color: $darkgrey;
    background: transparent;
    cursor: pointer;
    // border: none;
    // border-radius: 20px;
    border: 1px solid $main-bggrey;

    &:hover {
        color: black;
        background: $main-grey;
        border: 1px solid $main-grey;
    }
}

.langlistbuttonactive {
    color: white;
    background: $darkgrey;
    border: 1px solid $darkgrey;
}

.listborder_on {
    border: 1px solid $darkgrey;
}

.listbg_on {
    background: $main-bggrey;
}

.langtitle {
    font-size: 64px;
    line-height: 70px;
    padding-top: 25px;
}

.langtitle_next {
    font-size: 36px;
    padding-top: 25px;
    padding-bottom: 10px;
}

.langparams {
    width: 40%;
    // width: ($lettercube * 6 + 6px);
    margin: 0;
    padding: 35px 30px 20px 0;
    border-spacing: 0;
    font-size: 18px;
    line-height: 36px;
    height: fit-content;
}

.langlist {
    font-size: 18px;
    line-height: 27px;
    padding: 5px 15px 5px 0;
}

.tablenoborder {
    border: none;
}

.langlisttitle {
    color: $title;
    white-space: nowrap;
}

.langinfo {
    width: 60%;
    font-size: 18px;
    line-height: 27px;
    padding: 40px 40px 10px 0;
}

.langinfo_next {
    width: 80%;
    font-size: 18px;
    line-height: 27px;
    padding: 5px 40px 10px 0;
}

.tooltips_wrap {
    width: 80%;
    display: flex;
    gap: 10px;
    padding: 20px 0;
}

.tooltip_container {
    display: flex;
    width: 50%;
    padding: 0 20px 0 0;
}

.tooltip_avatar {
    border-radius: 50%;
}

.tooltips_info {
    // margin-top: -3px;
    padding: 0 0 0 15px;
}

// Table

.tablecontainer {
    padding: 20px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        width: 12px;
    }

    &::-webkit-scrollbar-track {
        background: white;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $main-grey;
        border-radius: 20px;
        border: 3px solid white;
    }
}

.tableheader {
    padding: 3px 3px 5px 20px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.tablewrap {
    display: flex;
    align-items: flex-start;
}

.tablestitle {
    font-size: 24px;
    line-height: 24px;
    padding: 25px 0 0 25px;
}

.tableinnertitle {
    font-size: 18px;
    line-height: 36px;
    padding-top: 15px;
    color: $title;
}

table {
    border-spacing: 25px 0;
    padding-bottom: 30px;
    // white-space: nowrap;
}

.tablerow {
    margin: 0 -25px -30px -25px;
}

th {
    text-align: left;
    width: 150px;
}

td {
    padding: 8px 0 8px 0;
    border-bottom: 1px solid $main-grey;
    vertical-align: top;
}

.letterchell {
    width: 70px;
}

.infochell {
    width: 220px;
}

.infochell_pc {
    width: 450px;
}

.containerrow {
    border-bottom: none;
    padding: 0;
}

.copytablebtn {
    border-left: 1px solid $main-grey;
    border-top: none;
    border-right: none;
    border-bottom: none;
    background: transparent;
    margin-left: 20px;
    padding: 0 20px;
    color: $darkgrey;
    font-size: 16px;
    cursor: pointer;
}


// Legend
@import "./legend";

// .legendbgcontainer {
//     position: sticky;
//     top: 10px + $topmenuheight;
//     z-index: 500;
//     width: fit-content;
// }

.definfocontainer {
    display: flex;
}

// .legendtextwrap {
//     display: flex;
//     margin: 0;
//     padding: 0 0 0 20px;
//     list-style-type: none;
// }

.definfowrap60 {
    width: 50%;
    padding: 10px 25px 10px 0;
    margin-top: 1px;
}

.definfowrap40 {
    width: 50%;
    padding: 10px 0 10px 25px;
    margin-top: 1px;
}

.definfotext {
    padding: 0 0 10px 0;
}

.legendinfotextnotitle {
    padding: 25px 0 10px 0;
}

.deftitle {
    color: $title;
    padding-top: 25px;
}


