// Letters

.lettersblock {
    padding: 0 0 50px 0;
}

.smalltitle {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    padding: 35px 15px 15px 0;
}

.smalltitlebtn {
    font-size: 12px;
    text-decoration: underline;
    cursor: pointer;
    background: transparent;
    border: none;
    // color: #6F6E6E;
    text-transform: uppercase;
}

.smalltitlebtnclose {
    text-align: center;
    font-size: 12px;
    line-height: 1;
    cursor: pointer;
    // background: $main-bggrey;
    background: transparent;
    border: none;
    border-radius: 50%;
    color: $grey-txt;
    // padding: 0 4px 1px 4px;
    margin: 0 11px 0 2px;
    text-transform: uppercase;
}

.smalltitlebtnright {
    padding-right: 10px;
    padding-left: 2px;
}

.smalltitlebtnleft {
    padding-left: 10px;
}

.lettersshowtext {
    font-size: 16px;
    color: $darkgrey;
    background: transparent;
    border: none;
    // text-transform: uppercase;
    cursor: pointer;
    padding: 0 0 50px 0;
    // margin: 0 0 0 32px;
    // outline: 1px solid $main-grey-equ;
}

.altborder {
    outline: 2px solid rgba($darkgrey, 0.35);
    outline-offset: -2px;
    // outline: 1px solid $darkgrey;
}

.altborder_in {
    display: flex;
    flex-wrap: nowrap;
    gap: 1px;
}

.smallunititle {
    position: absolute;
    font-size: 10px;
    width: $lettercube;
    padding: 3px;
}

.letter {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    text-align: center;
    width: $lettercube;
    height: $lettercube;
    background: $main-grey;
    cursor: pointer;

    // &:hover {
    //     position: relative;
    //     opacity: 0.9;
    //     filter: alpha(opacity=90);
    // }
}

.bigletter {
    font-size: 30px;
    line-height: 32px;
    padding-top: 21px;
}

.biglettersans {
    font-family: 'PT-Sans-Expert', sans-serif;
}

.bigletterserifitalic {
    font-family: 'PT-Serif-Expert-Italic', sans-serif;
}

.bigletteritalic {
    font-family: 'PT-Sans-Expert-Italic', sans-serif;
}

.bigletterserif {
    font-family: 'PT-Serif-Expert', sans-serif;
}

.markplace {
    text-align: right;
    // padding: 0 5px 10px 10px;
    margin: 0 4px 1px 5px;
}

.altmark {
    width: 32px;
    text-align: center;
    font-size: 9px;
    padding: 0 4px 1px 4px;
    border-radius: 5px;
    color: $grey-txt;
    background: $white-transparent;
}

.highlighted_letter {
    // color: $title;
    outline: 1px solid black;
    outline-offset: -1px;
}

.selectedsign {
    background: $main-grey;
}

.unselectedsign {
    // background: $main-bggrey;
    opacity: .5;
}

.selectedletter {
    color: black;
}

.unselectedletter {
    color: $grey-txt;
}

.unselecteduni {
    color: #c5c5c5;
}

.legendcolorsample {
    // padding: 5px 0;
    margin: 0 18px 7px 0;
    width: 32px;
    text-align: center;
    font-size: 9px;
    padding: 0 4px 1px 4px;
}

//Colors of letters
@import "./lettercolors";

// Media
@import "./media";