.wrap_total {
    // max-width: 1270px;
    width: 100%;
    min-height: 100vh;
    padding: 0 30px;
    background-color: $blackbg;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}

.wrap {
    display: flex;
    width: 100%;
    flex-grow: 1;
    position: relative;
    background-color: white;
}

.container {
    width: calc(100% - 60px);
    padding: 0 20px;
}


.langmenu {
    position: sticky;
    z-index: 800;
    top: $topmenuheight + 10px;
    width: 20%;
    min-width: 160px;
    padding: 5px 20px;
    margin: 10px 0;
    text-align: left;
    overflow-y: scroll;
    overflow-x: hidden;
    height: calc(100vh - 70px);

    &::-webkit-scrollbar {
        width: 12px;
    }

    &::-webkit-scrollbar-track {
        background: white;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $main-bggrey;
        border-radius: 20px;
        border: 3px solid white;
    }
}

.wrap_topmenu {
    position: sticky;
    padding: 0 20px;
    top: 0;
    z-index: 900;
    width: 100%;
    background-color: #243235;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    height: $topmenuheight;
}

.wrap_mobmenu {
    display: none;
    position: sticky;
    padding: 0 20px;
    top: 0;
    z-index: 900;
    width: 100%;
    background: #243235;
    align-items: center;
    color: white;
}